<template>
  <div class="swiper-slide pd10 border-box overflow-y-auto">
    <section id="page_main">
        <article class="pd15 bg-fff-0_6 mg-b10 border-eee">
            <h4 class="mp0 mg-b10">현재 퀴즈</h4>
            <img src="../assets/img/20210712_1.jpg" class="block w100p mg-b10" />
            <div class="mg-b10">
                <label class="input-style1 w100p bg-fff">
                    <input type="text" placeholder=" " v-model="state.newAnswer" />
                    <strong class="color-bbb">정답을 입력해주세요.</strong>
                    <span></span>
                </label>
            </div>
            <div class="txt-c">
                <button id="btn_submit" class="w80 pd10 border0 outline0 mg-r5" style="background: #f7f00f"
                @click="writeTheAnswer(1);">
                    <strong>제출</strong>
                </button>
                <button id="btn_hint" class="pd10 border0 outline0" style="background: #887">
                    <img src="../assets/img/hint.svg" height="16" class="scale1_5" />
                </button>
            </div>
        </article>
        <article id="box_quized" class="pd15 bg-fff-0_6 border-eee">
            <h4 class="mg0 pd-v5 border-b-dashed-ccc">이전 퀴즈</h4>
            <ul id="quizedList" class="list-style-none mp0">
                <li class="border-b-eee pd10" @click="getOldQuiz(q.seq);" v-for="q in state.quizList" :key="q.seq">
                    <h5 class="mg0 mg-b10">2021-07-12 아침 퀴즈<strong>(정답률: 42%)</strong></h5>
                    <div class="cf">
                        <div class="float-l w30p"
                        :class="{ 'w100p': state.viewOldQuiz === q.seq }">
                            <img src="../assets/img/20210712_1.jpg" class="block w100p" />
                            <img src="../assets/img/20210712_1.jpg" class="block w100p mg-t5"
                            v-if="state.viewOldQuizHint == q.seq" />
                        </div>
                        <div class="float-r w70p pd-l10 border-box" v-if="state.viewOldQuiz !== q.seq">
                            <ul class="mp0 list-style-none font12">
                              <li>1등: {{q.seq}}</li>
                              <li>2등: wondaero2</li>
                              <li>3등: mike</li>
                            </ul>
                        </div>
                    </div>
                    <div class="mg-t5" v-if="state.viewOldQuiz === q.seq">
                        <div class="mg-b5">
                            <label class="input-style1 w100p bg-fff">
                                <input type="text" placeholder=" " v-model="state.oldAnswer" />
                                <strong class="color-bbb">정답을 입력해주세요.</strong>
                                <span></span>
                            </label>
                        </div>
                        <div class="txt-c">
                            <button id="btn_submit" class="w80 pd10 border0 outline0 mg-r5" style="background: #f7f00f"
                            @click="writeTheAnswer(0);">
                                <strong>제출</strong>
                            </button>
                            <button id="btn_hint" class="pd10 border0 outline0 mg-r5" style="background: #887;"
                            @click="getOldQuizHint(q.seq);">
                                <img src="../assets/img/hint.svg" height="16" class="scale1_5" />
                            </button>
                            <button id="btn_answer" class="pd10 border0 outline0" style="background: #15c000;">
                                <img src="../assets/img/checked.svg" height="16" class="scale1_5" />
                            </button>
                        </div>
                    </div>
                </li>
            </ul>
        </article>
    </section>
  </div>
</template>
<script>
import { reactive, ref } from "@vue/reactivity";

export default {
  setup() {
    const state = reactive({
      newAnswer: '',
      oldAnswer: '',
      viewOldQuiz: '',
      viewOldQuizHint: '',
      quizList: [
        {seq: 1},
        {seq: 2},
        {seq: 3},
        {seq: 4},
        {seq: 5},
        {seq: 6}
      ]
    })

    //ref
    const newAnswer = ref(null);
    //ref


    //method
    const writeTheAnswer = (isNew) => {
      let answer = '';
      answer = isNew ? state.newAnswer : state.oldAnswer;

      if(confirm('\'' + answer + '\' (으)로 제출하시겠습니까?')){
        alert('제출되었습니다.');
        isNew ? state.newAnswer = '' : state.oldAnswer = '';
      }
    }

    const getOldQuiz = (oldQuizSeq) => {
      state.viewOldQuiz = oldQuizSeq;
      state.oldAnswer = '';
      state.viewOldQuizHint = '';
    }

    const getOldQuizHint = (oldQuizSeq) => {
      state.viewOldQuizHint = oldQuizSeq;
      console.log(state.viewOldQuizHint);
    }
    //method



    return {
      //method
      writeTheAnswer,
      getOldQuiz,
      getOldQuizHint,
      //method

      //ref
      newAnswer,
      //ref

      //var
      state,
      //var
    }
  },
}
</script>